import React from 'react';
import { Carousel, Row, Col } from 'antd';
import './LandingCarouselStyle.css';

const settingsCarousel={
    autoplay: true,
    dots: true
}
export default function LandingCarousel(){
    return(
        <>
            <div className='carousel-turn-table-landing'>
                <Carousel {...settingsCarousel} >
                    <div className='land-carousel-space-1'>
                        <div className='image-content-carousel-1'>
                        <p className='image-header-carousel-1'>Aos de Calidad</p>
                        <p className='image-text-carousel-1'>Contamos con ms de 40 aos de experiencia</p>
                        </div>
                    </div>
                    <div className='land-carousel-space-2'>
                        <div className='image-content-carousel-2'>
                        <p className='image-header-carousel-2'>Energa Alterna</p>
                        <p className='image-text-carousel-2'>Manejamos servicio de instalacin de enregas renovables</p>
                        </div>
                    </div>
                </Carousel>
            </div>
        </>
    )
}